body, html {
    margin: 0;
    padding: 0;
    font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
    font-weight: 300;
    line-height: 1.3;
    background-color: #fff;
    height: 100%;
    min-width: 360px;
}

a, a:hover, a:visited {
    color: #DF4A00;
}

.full-height {
    height: 100%;
}

header {
    color: #fff;
    background-color: #103D5D;
    padding: 33px 0 25px 15px;
}

strong {
    color: #103D5D;
}

header p {
    width: 400px;
    margin: 0 auto;
}

header .wrapper {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
}

header h1 {
    font-size: 50px;
    font-weight: 600;
    letter-spacing: 0.3px;
    display: inline-block;
    margin: 0;
    vertical-align: middle;
}

#logo {
    vertical-align: text-bottom;
    margin-right: 10px;
}

.nobreak {
    white-space: nowrap;
}

.center {
    text-align: center;
}

.not-found {
    line-height: 1;
    color: #666;
    margin: 0 20px;
    font-weight: bold;
}

#pietjesbak-marker {
    opacity: 0.7;
}

ul.pagination {
    text-align: center;
    padding: 0;
    margin: 0;
}

ul.pagination li {
    border: 1px solid #999;
    list-style: none;
    display: inline-block;
    border-radius: 10%;
}

ul.pagination li a {
    padding: 5px 10px;
    display: inline-block;
    cursor: pointer;
    color: #103D5D;
    height: 20px;
}

ul.pagination li:hover a, ul.pagination li.active a {
    background: #103D5D;
    color: #fff!important;
    font-weight: bold;
}

h2 {
    font-size: 1.8em;
    font-weight: bold;
    margin: 20px 0;
}

h3 {
    font-size: 1.5em;
    font-weight: bold;
    margin: 20px 0 10px 0;
}

.pointer {
    cursor: pointer;
}

span.whitespace-normal {
    display: inline-block;
    white-space: initial;
    line-height: 1.4;
    vertical-align: text-top;
}

.spinner {
    font-size: 2em;
    display: block;
    text-align: center;
    color: #103D5D;
}

i {
    vertical-align: middle;
}

.mute {
    font-size: smaller;
    font-weight: normal;
    font-style: italic;
    color: #666;
}

.hidden {
    display: none!important;
}
